import QRCode from 'qrcode';

/**
 * Générer un QR code pour une séance
 */
export const generateQRCode = async (seanceId: string, _seanceName: string): Promise<string> => {
  try {
    const baseUrl = window.location.origin;
    const voteUrl = `${baseUrl}/votepublic/seance/${seanceId}`;
    
    const qrCodeDataUrl = await QRCode.toDataURL(voteUrl, {
      width: 300,
      margin: 2,
      color: {
        dark: '#000000',
        light: '#FFFFFF'
      }
    });
    
    return qrCodeDataUrl;
  } catch (error) {
    console.error('Erreur lors de la génération du QR code:', error);
    throw new Error('Impossible de générer le QR code');
  }
};

/**
 * Télécharger un QR code
 */
export const downloadQRCode = async (seanceId: string, seanceName: string): Promise<void> => {
  try {
    const qrCodeDataUrl = await generateQRCode(seanceId, seanceName);
    
    // Créer un lien de téléchargement
    const link = document.createElement('a');
    link.href = qrCodeDataUrl;
    link.download = `qr-code-${seanceName.replace(/[^a-z0-9]/gi, '-').toLowerCase()}.png`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  } catch (error) {
    console.error('Erreur lors du téléchargement du QR code:', error);
    throw new Error('Impossible de télécharger le QR code');
  }
};

/**
 * Imprimer un QR code
 */
export const printQRCode = async (seanceId: string, seanceName: string): Promise<void> => {
  try {
    const qrCodeDataUrl = await generateQRCode(seanceId, seanceName);
    
    // Créer une nouvelle fenêtre pour l'impression
    const printWindow = window.open('', '_blank');
    if (!printWindow) {
      throw new Error('Impossible d\'ouvrir la fenêtre d\'impression');
    }
    
    printWindow.document.write(`
      <!DOCTYPE html>
      <html>
        <head>
          <title>QR Code - ${seanceName}</title>
          <style>
            body {
              font-family: Arial, sans-serif;
              text-align: center;
              padding: 20px;
              margin: 0;
            }
            .qr-container {
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: center;
              min-height: 100vh;
            }
            .qr-code {
              margin: 20px 0;
            }
            .seance-info {
              margin: 20px 0;
              font-size: 18px;
            }
            .instructions {
              margin: 20px 0;
              font-size: 14px;
              color: #666;
            }
            @media print {
              body { margin: 0; }
              .qr-container { min-height: auto; }
            }
          </style>
        </head>
        <body>
          <div class="qr-container">
            <h1>QR Code - ${seanceName}</h1>
            <div class="qr-code">
              <img src="${qrCodeDataUrl}" alt="QR Code pour ${seanceName}" />
            </div>
            <div class="seance-info">
              <p><strong>Séance:</strong> ${seanceName}</p>
              <p><strong>URL:</strong> ${window.location.origin}/votepublic/seance/${seanceId}</p>
            </div>
            <div class="instructions">
              <p>Scannez ce QR code pour accéder à la page de vote de cette séance</p>
            </div>
          </div>
        </body>
      </html>
    `);
    
    printWindow.document.close();
    printWindow.focus();
    
    // Attendre que le contenu soit chargé avant d'imprimer
    setTimeout(() => {
      printWindow.print();
      printWindow.close();
    }, 500);
  } catch (error) {
    console.error('Erreur lors de l\'impression du QR code:', error);
    throw new Error('Impossible d\'imprimer le QR code');
  }
}; 